import React, { Component } from 'react';


import TableHead from './table-head';
import TableRow from './table-row';

import mentorStudent from './mergeData.json'

import './table.css'
class App extends Component {
  constructor(props){
    super(props);
    this.data = mentorStudent;
    this.state = {
      currentValue: this.data[0]
    }

    this.handleSelectChange = this.handleSelectChange.bind(this);
  }

  componentWillMount(){
    const currentMentorGitNick = localStorage.getItem('currentMentor');
    for(let i = 0; i < this.data.length; i++){
      if(this.data[i].mentorGitNick === currentMentorGitNick){
        this.setState({
          currentValue: this.data[i]
        })
      }
    }
  }


  handleSelectChange(event){
    let currentMentor;
    for(let i = 0; i < this.data.length; i++){
      if(this.data[i].mentorGitNick === event.target.value){
        currentMentor = this.data[i];
      }
    }
    this.setState({
      currentValue: currentMentor
    });

    localStorage.setItem('currentMentor', event.target.value);
  }

  render() {
    return (
      <div className='students-score'>
        <div className='select__wrapper'
        style={{
          display: 'flex',
          justifyContent: 'center'
        }}>
          <span style={{marginRight: '10px'}}>Select mentor: </span>
          <select id='mentorSelect'
            value = {this.state.currentValue.mentorGitNick}
            onChange = {this.handleSelectChange}
          >
            {
              this.data.map((mentor) => {
                return <option key={mentor.mentorGitNick.toString()}
                  value={mentor.mentorGitNick}
                >{mentor.mentorGitNick}</option>
              })
            }
          </select>
        </div>
        
        
        <h2 className='tableTitle' style={{
          textAlign: 'center'
        }}>Students Score</h2>
        <table className='score-table'>
          <TableHead />
          <tbody>
            {
              this.state.currentValue.mentorStudentsMarks.map((item, i) => {
                return <TableRow data={item} key={i + item.studentGit.toString()}/>
              })
            }
          </tbody>
        </table>
      </div>
    );
  }
}

export default App;
