import React, { Component } from 'react';

import './table-head.css'

import tasksStatus from './tasksStatus.json'

class TableHead extends Component{
  render(){
    return (
      <thead>
        <tr className='table-head'>
          <th>Students\Tasks</th>
          {
            tasksStatus.map((task) => {
              return <th className={task.status} key={task.taskTitle.toString()}>
                {task.taskTitle}
              </th>
            })
          }
        </tr>
      </thead>
    )
  }
}

export default TableHead;