import React, { Component } from 'react';

import './table-row.css'

class TableRow extends Component{
  constructor(props){
    super(props);

    this.data = props.data;
  }
  render(){
    
    return(
      <tr className='student'>
        <td>{this.data.studentGit}</td>
        {
          this.data.studentMarks.map((item, i) => {
            if(typeof item.taskMark === 'undefined'){
              return <td className='no-mark' key={i + 'mark'}></td>
            }
            return <td className='mark' key={i + 'mark'}>{item.taskMark}</td>
          })
        }
      </tr>
    )
  }
}

export default TableRow;